import './App.css';
import { useRef, useState } from 'react';
import CheckSection from './components/CheckSection';
import Dubyk from './angryDubyk.png';
import Sax from './sax.png';
import SpinnyDubyk from './dubie.jpeg';

function App() {
	const [playText, setPlayText] = useState(["Concert Bb", "Major Scale"]);
	const transition = useRef(null);
	const btn = useRef(null);

	const [scales, setScales] = useState([
		{label: "Concert Bb", checked: true},
		{label: "Concert Eb", checked: true},
		{label: "Concert Ab", checked: true},
		{label: "Concert Db", checked: true},
		{label: "Concert Gb", checked: true},
		{label: "Concert B", checked: true},
		{label: "Concert E", checked: true},
	]);

	const [exercises, setExercises] = useState([
		{label: "Major Scale", checked: true},
		{label: "Minor Scale", checked: true},
		{label: "Chromatic", checked: true},
		{label: "Arpeggio", checked: true},
		{label: "Thirds", checked: true},
		{label: "Fourths", checked: true},
		{label: "Fifths", checked: true},
		{label: "Forms 1-4", checked: true},
	]);
	
	let data = JSON.parse(window.localStorage.getItem("scaleData"));

	if(data !== null){
		// technically only need to store checked 
		// but I rolled out the shit already and changing it now would
		// break it for people already using it
		console.log(data);
		let tempScales = scales;
		let tempExercises = exercises;

		tempScales.forEach((scale, i) => {
			scale.checked = data[0][i].checked;
		});

		tempExercises.forEach((exercise, i) => {
			exercise.checked = data[1][i].checked;
		});
	}

	window.onbeforeunload = () => {
		window.localStorage.setItem("scaleData", JSON.stringify([scales, exercises]));
	}

	function selectRandom(){
		let tempKeys = [];
		let tempExercises = [];

		for(let i = 0; i < scales.length; i++){
			if(scales[i].checked) tempKeys.push(scales[i].label);
			if(i === scales.length - 1 && tempKeys.length === 0) tempKeys.push("Select Keys Below");
		}

		for(let i = 0; i < exercises.length; i++){
			if(exercises[i].checked) tempExercises.push(exercises[i].label);
			if(i === exercises.length - 1 && tempExercises.length === 0) tempExercises.push("Select Exercises Below");
		}

		let randExercise = Math.floor(Math.random() * tempExercises.length);

		switch(tempExercises[randExercise]){
			case "Forms 1-4":
				tempExercises = "Form " + (Math.floor(Math.random() * 4) + 1);
				break;
			case "Major Scale":
				tempExercises = "Major Scale with Arpeggio";
				break;
			case "Chromatic":
				tempExercises = "Chromatic Scale";
				break;
			default:
				tempExercises = tempExercises[randExercise];
		}


		transition.current.style = "-webkit-animation:transition 1.5s 1; -moz-animation:transition 1.5s 1; animation:transition 1.5s 1";
		btn.current.style = "pointer-events: none;";
		setTimeout(()=>{
			setPlayText([tempKeys[Math.floor(Math.random() * tempKeys.length)], tempExercises]);
		}, 750);
		setTimeout(()=>{
			transition.current.style = "animation: none";
			btn.current.style = "pointer-events: all;";
		},1500);
	}

	function updateScales(newScales){
		setScales(newScales);
	}

	function updateExercises(newExercises){
		setExercises(newExercises);
	}

    return (
		<div className="center">

			{/* <h1 id="title">Dubyks Scales!</h1> */}

			<div className="main">
				<div className="above">

					<h1 className="key">{ playText[0] }</h1>
					<h2>{ playText[1] }</h2>
					
					<div className="checkboxGrid">
						<button ref={btn} className="defaultBtn" onClick={ selectRandom }>RANDOMIZE</button>
					</div>

					<img src={SpinnyDubyk} ref={transition} className="spinnyDubyk" alt="Jerrold Dubyk"/>

				</div>
				
				<img src={Dubyk} width="400" className="dubyk" alt="Jerrold Dubyk, highschool band teacher extraordinaire"/>
				<img src={Sax} width="100" className="sax" alt="saxophone"/>

			</div>


			
			{/* <Metronome/> */}
			<CheckSection title="Keys" checkArray={scales} updateArray={ updateScales }/>
			<CheckSection title="Exercises" checkArray={exercises} updateArray={ updateExercises }/>
			

		</div>
    );
}

export default App;
